<template>
  <div class="xmxx-main">
    <div class="container-mini">
      <div class="content">
        <div class="common-main box">

          <div class="page-list">
            <div class="pagelist-left">采购方式</div>
            <div class="pagelist-right">{{ CGFS[wybmListItem.cgfs] }}</div>
          </div>

          <div class="page-list">
            <div class="pagelist-left">项目状态</div>
            <div class="pagelist-right">{{ XMZT[wybmListItem.xmzt] }}</div>
          </div>
          <div class="page-list">
            <div class="pagelist-left">报名截止时间</div>
            <div class="pagelist-right">{{ getBmjzsj() }}</div>
          </div>
          <div class="page-list">
            <div class="pagelist-left">开标时间</div>
            <div class="pagelist-right">{{ getKbsj() }}</div>
          </div>
          <div class="page-list">
            <div class="pagelist-left">采购人联系人</div>
            <div class="pagelist-right">{{ wybmListItem.lxr }}</div>
          </div>

          <div class="page-list">
            <div class="pagelist-left">联系电话</div>
            <div class="pagelist-right">{{ wybmListItem.lxdh }}</div>
          </div>

          <div class="page-list">
            <div class="pagelist-left">手机</div>
            <div class="pagelist-right">{{ wybmListItem.sj }}</div>
          </div>

          <div class="page-list">
            <div class="pagelist-left">传真</div>
            <div class="pagelist-right">{{ wybmListItem.cz }}</div>
          </div>

          <!-- <van-cell class="cell-list" title="采购方式" :value="CGFS[wybmListItem.cgfs]" />
          <van-cell class="cell-list" title="项目状态" :value="XMZT[wybmListItem.xmzt]" />
          <van-cell class="cell-list" title="开标时间" :value="getTime()" />
          <van-cell class="cell-list" title="报名截止时间" :value="getTime()" />
          <van-cell class="cell-list" title="采购人联系人" :value="wybmListItem.lxr" />
          <van-cell class="cell-list" title="联系电话" :value="wybmListItem.lxdh" />
          <van-cell class="cell-list" title="手机" :value="wybmListItem.sj" />
          <van-cell class="cell-list" title="传真" :value="wybmListItem.cz" /> -->
          <!--            <van-cell class="cell-list" title="是否同意邀请" :value=  SFZGYS[this.wybmListItem.sftyyq]>-->
          <!--            </van-cell>-->
        </div>
      </div>

      <div v-if="fbxx.gysyqk" class="content">
        <div class="common-main box" @click="yqhckAction">
          <div class="page-list">
            <div class="pagelist-left"><span class="icon-yqh">邀请函下载</span></div>
            <div class="pagelist-right more">
              <i class="iconfont iconarrowright" />
            </div>
          </div>
        </div>
      </div>
    </div>

    <van-popup v-model="status.isShowPublish" closeable :style="{width: '90%', height: '90%' }">
      <div class="popup-title">邀请函查看</div>
      <div class="popup-content" v-html="fbxx.gysyqk" />
      <div v-if=" wybmListItem.sftyyq !== 1 " class="popup-btn">
        <ul>
          <li @click="closeDialog">取消</li>
          <li @click="yqhqrAction(0)">拒绝邀请</li>
          <li class="select" @click="yqhqrAction(1)">接受邀请</li>
        </ul>
      </div>
      <div v-else class="popup-btn">
        <ul>
          <li class="select" @click="closeDialog">完成</li>
        </ul>
      </div>

    </van-popup>

  </div>
</template>

<script land="script" scoped>

import constants from '@/common/js/constants'
import { formatTimes } from '@/common/js/util'
import { mapState, mapMutations } from 'vuex'
export default {
  components: {

  },
  props: {
    wybmListItem: {
      type: null,
      required: true
    }, fbxx: {
      type: null,
      required: true
    }
  },
  data() {
    return {
      qyhzt: null, // 邀请函状态
      swiperToNav: 0, // swiper滑动下标值传给nav导航
      status: {
        isShowPublish: false,
        topNum: 30
      }
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.mh.mhUserInfo
      // wybmListItem: state => state.wybm.wybmListItem,
      // gysyqk: state => state.wybm.yqhck
    })
  },
  created() {
    this.XMZT = constants.XMZT
    this.CGFS = constants.CGFS
    this.SFZGYS = constants.SFZGYS
  },
  methods: {
    ...mapMutations([
      'SET_YQHZT'
    ]),
    getBmjzsj() {
      var time = formatTimes(this.wybmListItem.bmjzsj)
      return time
    },
    getKbsj() {
      var time = formatTimes(this.wybmListItem.kbsj)
      return time
    },
    closeDialog() {
      this.status.isShowPublish = false
      // if (this.fbxx.gysyqk !== null && this.this.fbxx.gysyqk !== 0) {
      //   this.status.isShowPublish = false
      // } else {
      //   this.$toast.fail('暂无数据！')
      // }
    },
    yqhqrAction(type) {
      this.$store.dispatch('gyswybm/getGysyqhqrAction', {
        zzid: this.userInfo.zzid,
        xmid: this.wybmListItem.xmid,
        sftyyq: type
      }).then((res) => {
        this.res = res
        // this.SET_YQHZT(type)
        this.$store.commit('gyswybm/SET_YQHZT', type)
        this.status.isShowPublish = false
        if (type === 1) {
          this.wybmListItem.sftyyq = 1
          this.$emit('agree')
        } else {
          this.status.isShowPublish = false
        }
      // eslint-disable-next-line no-unused-vars
      })
    },
    yqhckAction() {
      this.status.isShowPublish = true
      // this.SET_GO_WYBM('true')
    }

  }
}
</script>

<style lang="css" scoped>
.xmxx-main{height:calc(100vh - 180px);}
</style>
