<template>
  <div>
    <div class="container-main">
      <div v-if="pageStyles == 0 ">
        <!-- <xy-scroll
          ref="scrollContent"
          :data="WgfbLists"
          class="wgfb-wrapper"
          :pulldown="pulldown"
          :pullup="pullup"
          @pulldown="getNewData"
          @scroll="refreshTip"
          @scrollToEnd="scrollToEnd"
        > -->
        <div class="wgfb-box">
          <div class="common-main">
            <div class="common-list sub-list">
              <div
                v-for="(item, index) in WgfbLists"
                :key="index"
                class="list box"
              >
                <h3>
                  <div>
                    <span v-if="state!==2" class="check">

                      <input
                        :checked="checkIds.indexOf(item.bid)>=0"
                        class="input-checkbox"
                        name="checkboxinput"
                        type="checkbox"
                        @click="checkedOne(item.bid)"
                      >

                      <span class="che default">
                        <i class="iconfont iconxuanze" />
                      </span>
                      <span class="che checked">
                        <i class="iconfont iconxuanzhong" />
                      </span>
                    </span>
                    {{ item.bmc }}
                  </div>

                </h3>

                <div class="info">
                  <span>
                    <p class="less-txt">包编号:{{ item.bbh }}</p>
                  </span>
                </div>

                <div v-if="checkOpen(index)" class="description">
                  <span class="arrow" @click="showMore(index)">
                    <i class="iconfont iconarrow-down" />
                  </span>
                  <p class="less-txt">包描述:{{ item.bms }}</p>
                </div>

                <div v-else class="description" @click="checkOpen(index)">
                  <span class="arrow" @click="showMore(index)">
                    <i class="iconfont iconarrow-down" />
                  </span>
                  <p class="less-txt">包描述:{{ item.bms }}</p>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- </xy-scroll> -->
      </div>
      <div v-else>
        <error-page
          :page-style="pageStyles"
          @errorButton="parentReceive"
        />
      </div>
    </div>
    <!-- <div v-if="state!==2">
      <div class="foot-btn fixed">
        <ul>
          <li v-if="checkIds.length > 0 && checkIds.length == WgfbLists.length" @click="checkedAll()">
            <div class="check-icon">
              <i class="iconfont iconxuanzhong" />
            </div>
            <p>全选</p>
          </li>
          <li v-else @click="checkedAll()">
            <div class="check-icon">
              <i class="iconfont iconxuanze" />
            </div>
            <p>全选</p>
          </li>
          <li><van-button type="info" size="large" round @click="addXmfbIds()">确定</van-button></li>
        </ul>
      </div>
    </div> -->
    <div class="common-space" />
  </div>
</template>
<script>

import constants from '@/common/js/constants'
import { mapState } from 'vuex'
import errorPage from '@/components/result-page/index'

export default {
  name: 'HelloWorld',
  components: {
    errorPage
    // [Popup.name]: Popup
  },
  props: {
    fbxx: {
      type: null,
      required: true
    },
    state: {
      type: null,
      required: true
    },
    isall: {
      type: null,
      required: true
    }
  },
  data() {
    return {
      checkIds: [],
      openIds: [],
      WgfbLists: [],
      // 初始化全选按钮, 默认不选
      isCheckedAll: false,
      isPopupShow: {},
      pageStyles: 0, // 页面状态展示
      listenScroll: true, // 监听刷新
      pulldown: false, // 下拉加载刷新
      pullup: false, // 上拉加载更多
      nomore: true // 没有更多数据
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.mh.mhUserInfo,
      wybmListItem: state => state.gyswybm.wybmListItem
      // wybmListItem: state => state.wybm.wybmListItem,
      // wgfbList: state => state.wybm.wgfbList,
      // ygfbList: state => state.wybm.ygfbList
      // yqhzt: state => state.wybm.yqhzt
      // gysyqk: state => state.wybm.yqhck
    })
  },
  watch: {
    isall(value) {
      this.checkIds = value
    },
    fbxx(value) {
      this.parentReceive()
    }
  },
  created() {
    this.checkIds = this.isall
    this.XMZT = constants.XMZT
    this.CGFS = constants.CGFS
    // if (this.userInfo && this.userInfo.id) {
    // this.refreshData()
    // this.getWybmLists()
    // this.pageStyle = true
    // console.info('wgfbList-----', this.wgfbList[0].bmc)
    // console.info('ygfbList-----', this.ygfbList)
    if (this.fbxx.length === 0) {
      this.pageStyles = 3
    } else {
      this.WgfbLists = this.fbxx.wgfbList
      if (this.fbxx.wgfbList.length === 0) {
        this.pageStyles = 1
      }
    }
    // } else {
    //   this.pageStyles = -1
    // }
  },
  methods: {
    parentReceive() {
      this.$store
        .dispatch('gyswybm/getWybmFbListAction', { zzid: this.userInfo.zzid, xmid: this.wybmListItem.xmid })
        .then(res => {
          if (res === 2) {
            this.pageStyles = 3
          }
          // this.$bus.$emit('changeFbxx', res) // bus触发事件
          this.WgfbLists = res.wgfbList
          if (this.WgfbLists.length !== 0) {
            this.pageStyles = 0
          }
          this.$store
            .dispatch('gyswybm/setFbxxAction', res)
        })
        .catch((error) => {
          // this.pageStyles = 3
          console.log(error)
          // this.loading = false
        })
    },
    getNewData() {
      this.WgfbLists = this.fbxx.wgfbList
    },
    scrollToEnd() {
      this.$refs.scrollContent.shanglajiazai()
    },
    refreshTip(pos) {
      if (pos.y > 20) {
        this.$refs.scrollContent.refreshTip()
      }
    },
    checkOpen(items) {
      const idIndex = this.openIds.indexOf(items)
      if (idIndex >= 0) { // 如果已经包含就去除
        return false
      } else { // 如果没有包含就添加
        return true
      }
    },
    showMore(items) {
      const idIndex = this.openIds.indexOf(items)
      if (idIndex >= 0) { // 如果已经包含就去除
        this.openIds.splice(idIndex, 1)
      } else { // 如果没有包含就添加
        this.openIds.push(items)
      }
    },
    checkedOne(bid) {
      console.info('bid-------', bid)
      const idIndex = this.checkIds.indexOf(bid)
      if (idIndex >= 0) { // 如果已经包含就去除
        this.checkIds.splice(idIndex, 1)
        this.$emit('checkIds', this.checkIds)
      } else { // 如果没有包含就添加
        this.checkIds.push(bid)
        console.log('checkIds-----------------------', this.checkIds)
        this.$emit('checkIds', this.checkIds)
      }
    },
    checkedAll() {
      if (this.checkIds.length > 0 && this.checkIds.length === this.WgfbLists.length) {
        this.checkIds = []
      } else {
        this.checkIds = []
        this.WgfbLists.forEach(item => {
          this.checkIds.push(item.bid)
        })
      }
    },
    addXmfbIds() {
      this.$store.dispatch('gyswybm/setLxrAction', [])
      // if (this.fbxx.gysyqk) {
      //   // if (this.wybmListItem.sftyyq !== 0 && this.wybmListItem.sftyyq !== 1) {
      //   //   this.$toast('请接受邀请函')
      //   // } else {
      //   // if (this.checkIds.length > 0) {
      //   this.$router.push({
      //     name: 'GysXmbm',
      //     params: { ids: this.checkIds, wybmListItem: this.wybmListItem, fbxx: this.fbxx
      //     }})
      //   // } else {
      //   //   this.$toast('请选择项目分包')
      //   // }
      //   // }
      // } else {
      if (this.checkIds.length > 0) {
        this.$store.commit('gyswybm/SET_WYBM_LIST_ITEM', this.wybmListItem)
        this.$store.commit('gyswybm/SET_FBXX', this.fbxx)
        this.$store.commit('gyswybm/SET_FBID', this.checkIds)
        this.$router.push({
          name: 'GysXmbm'
        })
      } else {
        this.$toast('请选择项目分包')
      }
    }
  }
}
</script>

<style lang= "css" scoped>

</style>
