<template>
  <div>
    <headerBar :header-data="headerData" />
    <!-- 项目信息 begin -->
    <div class="fixed-project" @touchmove.prevent>
      <div class="list">
        <h3>
          {{ wybmListItem.xmmc }}
        </h3>
        <div class="info">
          <span class="label">
            {{ CGFS[wybmListItem.cgfs] }}
          </span>
          <span>
            <i class="iconfont iconshouji" />
            {{ wybmListItem.sj }}
          </span>
          <span>
            <i class="iconfont iconshijian" />
            {{ wybmListItem.kbsj | formatTime }}
          </span>
        </div>
      </div>
    </div>
    <van-sticky class="tabs-sticky">
      <van-tabs class="van-tabs tabs" :active="active" swipeable animated sticky @change="showFoot">
        <van-tab v-for="(item ,index) in tabs" :key="index" :title="item">
          <xmxx v-if="index == 0 " :wybm-list-item="wybmListItem" :fbxx="fbxx" @agree="agree" />
          <wgfb v-if="index == 1 " :wybm-list-item="wybmListItem" :fbxx="fbxx" :state="state" :isall="checkIds" @checkIds="check" />
          <ygfb v-if="index == 2 " :wybm-list-item="wybmListItem" :fbxx="fbxx" />

        </van-tab>
      </van-tabs>
    </van-sticky>
    <!-- <FooterNav /> -->
    <div v-if="showFoots " class="foot-btn fixed">
      <ul>
        <li v-if="checkIds.length > 0 && checkIds.length == fbxx.wgfbList.length" @click="checkedAll()">
          <div class="check-icon">
            <i class="iconfont iconxuanzhong" />
          </div>
          <p>全选</p>
        </li>
        <li v-else @click="checkedAll()">
          <div class="check-icon">
            <i class="iconfont iconxuanze" />
          </div>
          <p>全选</p>
        </li>
        <li><van-button type="info" size="large" round @click="addXmfbIds()">确定</van-button></li>
      </ul>
    </div>
  </div>
</template>

<script>
import xmxx from '@/page/gys/wybm/xmxx/Bxmxx'
import wgfb from '@/page/gys/wybm/xmxx/Bwgfb'
import ygfb from '@/page/gys/wybm/xmxx/Bygfb'
import constants from '@/common/js/constants'
import { pageRouter } from '@/common/js/mixin.js'
import headerBar from '@/components/header/headerBar.vue'
// import FooterNav from '@/components/footerNav/footerNav.vue'

import { mapState } from 'vuex'
export default {
  name: 'Xmfb',
  components: {
    headerBar,
    xmxx,
    wgfb,
    ygfb
    // FooterNav
  },
  mixins: [pageRouter],
  data() {
    return {
      checkIds: [],
      headerData: {
        title: '项目信息',
        leftUrl: 'back',
        leftText: ' '
      },
      fbxx: [],
      active: 0,
      tabs: ['项目信息', '未报名分包', '已报名分包'],
      showFoots: false, // 是否展示下边栏
      state: 0 // 项目状态
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.mh.mhUserInfo,
      wybmListItem: state => state.gyswybm.wybmListItem
    })
  },

  created() {
    console.log('wybmListItem-----------------', this.wybmListItem)
    this.state = this.$route.params.state
    this.XMZT = constants.XMZT
    this.CGFS = constants.CGFS
    this.initData()
  },
  methods: {
    agree() {
      this.initData()
      this.wybmListItem.sftyyq = 1
    },
    showFoot(name, title) {
      if (name === 1) {
        this.showFoots = true
      } else {
        this.showFoots = false
      }
    },
    check(checkIds) {
      this.checkIds = checkIds
    },
    checkedAll() {
      if (this.checkIds.length > 0 && this.checkIds.length === this.fbxx.wgfbList.length) {
        this.checkIds = []
      } else {
        this.checkIds = []
        this.fbxx.wgfbList.forEach(item => {
          this.checkIds.push(item.bid)
        })
      }
    },
    addXmfbIds() {
      this.$store.dispatch('gyswybm/setLxrAction', [])
      // if (this.fbxx.gysyqk) {
      //   // if (this.wybmListItem.sftyyq !== 0 && this.wybmListItem.sftyyq !== 1) {
      //   //   this.$toast('请接受邀请函')
      //   // } else {
      //   // if (this.checkIds.length > 0) {
      //   this.$router.push({
      //     name: 'GysXmbm',
      //     params: { ids: this.checkIds, wybmListItem: this.wybmListItem, fbxx: this.fbxx
      //     }})
      //   // } else {
      //   //   this.$toast('请选择项目分包')
      //   // }
      //   // }
      // } else {
      if (this.checkIds.length > 0) {
        this.$store.commit('gyswybm/SET_WYBM_LIST_ITEM', this.wybmListItem)
        this.$store.commit('gyswybm/SET_FBXX', this.fbxx)
        this.$store.commit('gyswybm/SET_FBID', this.checkIds)
        this.$router.push({
          name: 'GysXmbm'
        })
      } else {
        this.$toast('请选择项目分包')
      }
    },
    initData() {
      this.$store
        .dispatch('gyswybm/getWybmFbListAction', { zzid: this.userInfo.zzid, xmid: this.wybmListItem.xmid })
        .then(res => {
          // this.$bus.$emit('changeFbxx', res) // bus触发事件
          this.fbxx = res
          this.$store
            .dispatch('gyswybm/setFbxxAction', res)
        })
        .catch((error) => {
          // this.pageStyles = 3
          console.log(error)
          // this.loading = false
        })
    },
    goBack() {
      this.$router.push({
        name: 'Wybm'
      })
    }

  }
}
</script>

<style scoped>
.nav-bar{box-shadow: none;}
</style>
