<template>
  <div>
    <div class="container-main">
      <div v-if="pageStyles == 0 ">
        <!-- <xy-scroll
          ref="scrollContent"
          class="ygfb-wrapper"
          :data="YgfbLists"
          @pulldown="getNewData"
          @scroll="refreshTip"
          @scrollToEnd="scrollToEnd"
        > -->
        <div class="ygfb-box">
          <div class="common-main">
            <div class="common-list sub-list">
              <div
                v-for="(item, index) in YgfbLists"
                :key="index"
                class="list box"
              >

                <h3><div class="tit">{{ item.bmc }}</div></h3>
                <div class="info">
                  <span>
                    <p class="less-txt">包编号:{{ item.bbh }}</p>
                  </span>
                </div>
                <div v-if="checkOpen(index)" class="description">
                  <span class="arrow" @click="showMore(index)">
                    <i class="iconfont iconarrow-down" />
                  </span>
                  <p class="less-txt">包描述:{{ item.bms }}</p>
                </div>

                <div v-else class="description" @click="checkOpen(index)">
                  <span class="arrow" @click="showMore(index)">
                    <i class="iconfont iconarrow-down" />
                  </span>
                  <p class="less-txt">包描述:{{ item.bms }}</p>
                </div>

              </div>
            </div>
          </div>
        </div>
        <!-- </xy-scroll> -->
      </div>
      <div v-else>
        <error-page
          :page-style="pageStyles"
          @errorButton="parentReceive"
        />
      </div>
    </div>
  </div>
</template>
<script land="script" scoped>
import constants from '@/common/js/constants'
import { mapState } from 'vuex'
import errorPage from '@/components/result-page/index'

export default {
  name: 'One',
  components: {
    errorPage
  },
  props: {
    fbxx: {
      type: null,
      required: true
    }
  },
  data() {
    return {
      openIds: [],
      YgfbLists: [],
      pageStyles: 0, // 页面状态展示
      listenScroll: true, // 监听刷新
      pulldown: false, // 下拉加载刷新
      pullup: false, // 上拉加载更多
      nomore: true // 没有更多数据
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.mh.mhUserInfo,
      wybmListItem: state => state.gyswybm.wybmListItem
      // ygfbList: state => state.wybm.ygfbList
    })
  },
  mounted() {
    window.ScrollViewTrigger1 = () => {
      this.$refs.scrollView.triggerRefresh()
    }
  },

  created() {
    this.XMZT = constants.XMZT
    this.CGFS = constants.CGFS
    // if (this.userInfo && this.userInfo.id) {
    this.YgfbLists = this.fbxx.ygfbList
    // } else {
    //   this.pageStyles = -1
    // }
    if (this.fbxx.length === 0) {
      this.pageStyles = 3
    } else {
      if (this.fbxx.ygfbList.length === 0) {
        this.pageStyles = 1
      }
    }
  },
  methods: {
    parentReceive() {
      this.$store
        .dispatch('gyswybm/getWybmFbListAction', { zzid: this.userInfo.zzid, xmid: this.wybmListItem.xmid })
        .then(res => {
          if (res === 2) {
            this.pageStyles = 3
          }
          // this.$bus.$emit('changeFbxx', res) // bus触发事件
          this.YgfbLists = res.ygfbList
          if (this.YgfbLists.length !== 0) {
            this.pageStyles = 0
          }
          this.$store
            .dispatch('gyswybm/setFbxxAction', res)
        })
        .catch((error) => {
          // this.pageStyles = 3
          console.log(error)
          // this.loading = false
        })
    },
    getNewData() {
      this.YgfbLists = this.wgfbList
    },
    scrollToEnd() {
      this.$refs.scrollContent.shanglajiazai()
    },
    refreshTip(pos) {
      if (pos.y > 20) {
        this.$refs.scrollContent.refreshTip()
      }
    },
    checkOpen(items) {
      const idIndex = this.openIds.indexOf(items)
      if (idIndex >= 0) { // 如果已经包含就去除
        return false
      } else { // 如果没有包含就添加
        return true
      }
    },
    showMore(items) {
      const idIndex = this.openIds.indexOf(items)
      if (idIndex >= 0) { // 如果已经包含就去除
        this.openIds.splice(idIndex, 1)
      } else { // 如果没有包含就添加
        this.openIds.push(items)
      }
    }
  }
}
</script>

<style lang="css" scoped>
.ygfb-main{height: calc(100vh - 180px);}
.scroll-wrapper{height: 400px;}
</style>
